/* Base styles */
* {
    font-family: 'Nunito', sans-serif;
    margin: 0px;
    padding: 0px;
   
    box-sizing: border-box;
}
body{
  width: 100%;
  height: 100%;
}

span {
    color: rgb(247, 125, 37);
}

.content {
    display: flex;
    flex-wrap: wrap; 
    gap: 6rem;
    padding-right: 9rem;
   
}

li{
    list-style-type: none;
    color: rgb(23, 23, 22);
    font-size: 1rem;
    
}

li:hover {
    color: rgb(247, 125, 38);
    cursor: pointer;
}

/* Navigation */
.nav {
    width: 100%;
    height: 60px;
    margin: 0;
    background-color: white;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: space-between; /* Spread items evenly */
    padding: 0 2rem; /* Add some space to the sides */
    position: fixed;
    z-index: 2;
    top: 0px;
}

.logo_text {
    display: flex;
    align-items: center;
}

.image {
    width: 4rem;
}

.text {
    font-size: 2rem;
    padding-top: 0.1rem;
    color: rgb(247, 112, 16);
}

.Toggle {
    display: none; /* Hide the toggle icon by default */
    cursor: pointer;
    font-size: 1.5rem;
    color: darkorange;
}


.Button {
    display: inline-block;
    padding: 10px 20px;
    margin-right: 2rem;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    color: white;
    background-color:rgb(247, 112, 16);
    cursor: pointer;
  
}

.Button a{
color: white;
}

.Button:hover {
    background-color: darkorange;
  
}

/* Media queries for responsiveness */
@media screen and (max-width: 768px) {
    .content {
        display: none; /* Hide the content links on smaller screens */
        position: absolute;
        top: 60px; /* Adjust based on your navbar height */
        left: 0;
        width: 100%;
        background-color: bisque;
        flex-direction: column;
        align-items: center;
        padding: 1rem;
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
        z-index: 1;
    }

    .content.active {
        display: flex; /* Show the content links when the menu is active */
    }

    .Toggle {
        display: block; /* Show the toggle icon on smaller screens */
        position: absolute;
        top: 20px;
        right: 20px;    }

       
}


