.card {
    margin: 60px 90px 90px 90px;
    width: 88%;
    height: 40.8rem;
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
    border-radius: 8px;
    color: white;
    overflow: hidden;
    position: relative;
    transform-style: preserve-3d;
    perspective: 1000px;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.20, 1);
    cursor: pointer;
  }
  
  .card-content {
  
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 30px;
    color: white;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 60%;
  }
  
  .card-content .card-title {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 70px;
    font-weight: 700;
    color: inherit;
    text-transform: uppercase;

  }
  
  .card-content .card-para {
    color: inherit;
    opacity: 0.8;
    font-size: 32px;
    width:80%;

  }
  


  .card:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.1));
    transition: transform 0.5s cubic-bezier(0.23, 1, 0.320, 1);
    z-index: 1;
  }
  
  .card:hover:after {
    transform: translateX(100%);
  }


