body{
    margin-top: 70px;
}
.ContactUs-h1{
    position:absolute;
    top: 10rem;
    font-size: 4rem;
    color: #170745;
    margin-left: 40rem;
  
}

.ContactUs-img{
    width: 100%;
    height: 20rem;
    filter: blur(2px);
}

.Contact-list-img{
    width: 7rem;
}

.ContactUs-imgs{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 3rem 0rem 0rem 30rem;
}

.ContactUs-info{
    display: flex;
    flex-direction: column;
    gap: 4rem;
    position: relative;
    font-size: 1rem;
    top: -24rem;
    margin:0rem 0rem -15rem 40rem;
}

.ContactUs-info h1{
   width: 40rem;
    font-size: 1.3rem;
   
}

.Map iframe{
    width: 75rem;
    height: 20rem;
   border: none;
   border-radius: 1rem;
    margin:0rem 0rem 2rem 14rem;
}