    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
} */
.Data-banner{
    background-image: url(../Components//Images//student.png);
    background-size: cover;
    background-position: center; 
    /* filter: blur(2px); */
    width: 100%;
    height: 35rem;
    margin-bottom: 3rem;
    
}


.Data-Maintext{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 3rem;
    text-align: center;
    color: white;
    padding-top: 2rem;
}
.Data-banner-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    background: rgba(255,255,255,0.05);
    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);
    border: 1px solid rgba(255,255,255,0.025);
    position: relative;
    top: 2rem;
    width: 100%;
    height: 26rem;
    
   
}

.Data-banner-content-count {
    display: flex;
    gap: 13rem;
    font-size: 2.2rem;
    color: orange;
    font-weight: 4px;
}

.Data-banner-content-count-text{
    display: flex;
   gap: 10rem;
   font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1.4rem;
    color: white;
}


@media (max-width: 768px) {
    .Data-Maintext {
        font-size: 1.5rem;
    }
    
    .Data-banner-content-count {
        gap: 3rem;
        font-size: 3rem;
        display: flex;
        flex-direction: column;
        position: relative;
        top: 10rem;
    }
    
    .Data-banner-content-count-text {
        gap: 3rem;
        display: flex;
        flex-direction: column;
        font-size: 1px;
        position: relative;
        top: -7.8rem;
        
        
    }
}


