
.Gallary-Banner{
    margin: 0px 0px 90px 0px;
   

}

.Gallary-Container{
    width: 100%;
    height: 40.8rem;
   background-image: url(.//Images/science\ book\ ga\ 0.png);
  display: flex;
  flex-direction: column;
 gap: 1rem;
 background-size: cover;
 background-position: center; 
 box-shadow: inset 30px 0px 30px 30px  rgba(0, 0, 0, 0.9);

}

.Gallary-Container h1{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 5rem;
  text-align: center;
  color: white;
}


.Gallary-Row1{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    
   
}


.Gallary-Row1 img{
    width: 30rem;
    /* width: 10rem; */
    z-index: 1;
    border-radius: 2rem;
    padding: 1rem;
}

@media screen and (max-width: 768px) {
    
.Gallary-Banner{
    margin: 50px 90px 50px 20px;
}

.Gallary-Container{
   
    width: 120%;
    height: 40.8rem;
    background-color:  rgb(248, 247, 251);
    border-radius: 16px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.241);
  display: flex;
  flex-direction: column;
 gap: 1rem;
   
}

.Gallary-Container h1{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 5rem;
  text-align: center;
}


.Gallary-Row1{
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
   
}


.Gallary-Row1 img{
    width: 10rem;
    
}
  }