/* --------------Event1-banner---------------- */



.Event1-main-banner{
    margin: 60px 90px 90px 90px;
  
    
  }
  
  
  .Event1-banner{
    width: 100%;
    height: 45rem;
    background-color:  rgb(248, 247, 251);
   
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.241);
  background-image: url(../Components/Images/edbackground.jpg);
  background-size: cover;
      background-position: center; 
  
      border-radius: 1rem;
  }
  
  
  .message {
    background-color: yellow;
    color: #333;
    display: inline-flex;
    font-weight:900;
    width: 12rem;
    overflow: hidden;
    padding-left: 0.5rem;
    animation: openclose 2s ease-in-out infinite;
    margin: 4rem;
    position: relative;
  }
  
  .word1{
    font-family: tahoma;
    font-size: 3rem;
  
  
  }
  
  @keyframes openclose {
    0% {
      top: 0.2rem;
      width: 0rem;
      
    }
   
  
    60% {
      top: -9rem;
      width: 12.5rem;
      
    }
    
  }
  
  .Event-carousel{
    width: 30rem;
   margin: 3rem 0rem 0rem 6rem;
    position: absolute;
    top: 238rem;  
  }
 

  
  .Event-head{
    text-align: end;
    margin: 1rem 13rem 0rem 0rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 70px;
  }
  
  
  
  
  
  
  
  .vcv-container h2{
    margin: 0 0 50px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color:white;
    text-align: center;
    font-size: 4rem;
  }
  
   p {
    margin: 0;
    font-family: "Roboto", sans-serif;
    line-height: 1.6;
    font-size: 20px;
    letter-spacing: 0.16px;
    font-weight: 300;
    color: white;
    text-decoration: none;
  }
  
  
  .vcv-container {
    padding: 30px;
    margin-left: 39rem;
   height: 40rem;
   overflow: hidden;
  }
  
  .vce {
    margin-bottom: 30px;
  }
  
  .vce-message-box {
    border-radius: 5px;
    padding: 17px 20px;
    font-size: 1em;
  }
  
  .vce-message-box {
    color: #fff;
  }
  
  .vce-message-box-inner {
    padding-left: 2.5em;
    position: relative;
  }
  
  .vce-message-box-icon {
    position: absolute;
    bottom: 0;
    left: 0;
    top: 50%;
    font-weight: 400;
    height: 100%;
    font-size: 1.7em;
    line-height: 1;
    font-weight: 400;
    font-style: normal;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: white;
  }
  
  
  
  .vce-message-box-style--success {
    background: #4bd67b;
  }
  
  .vce-message-box-style--information {
    background:#71b2df;
  }
  
  .vce-message-box-style--warning {
    background:#f5c76f;
  }
  
  .vce-message-box-style--error {
    background: #f57e7c;
  }
  
  
  .Marquee-content{
    display: flex;
    flex-direction: column;
  
    width: 40rem;
    height: 40rem;
    margin-top: 4rem;
  
  }



  /* ------------------------------------------------------------ */
  
  @media screen and (max-width: 768px) {
  
    .Event1-main-banner{
      margin: 10px 0px 0px 0px;
    
      
    }
  
    .Event1-banner {
      height: 30rem; /* Adjust height for smaller screens */
    }
    /* .vcv-container h1{
      margin: 0 0 50px;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      color:white;
      text-align: center;
      font-size: 4rem;
     
    } */
  
    .Event-carousel{
      width: 40%; 
      margin-left: 10rem; 
      position: relative;
      height: 5rem;
      top: 41.5rem;
    visibility: hidden;
    }
    
    .eventslideimg{
      background-size: cover;
      background-position: center; 
      width: 100%;
      height: 10rem;
      
    }
    .message {
      width: auto; /* Adjust width for smaller screens */
      margin: 2rem 0; /* Adjust margin for smaller screens */
    }
  
    
  
  
    .vcv-container {
      margin:-5rem 0rem 0rem -1rem;
      width: 28rem;
     display: flex;
  
  
     
    
    }
  
    /* Adjust font size and spacing for smaller screens */
    .vcv-container h2{
     
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
     color: white;
      font-size: 2rem;
    }
    p {
      font-size: 9px;
      
    }
    .Marquee-content{
      display: flex;
      flex-direction: column;
      width: 30rem;
    
    }
  
    
   
  }
  
  
  
  
  
  
  
  